import React from 'react';

import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
//App styled
import AS from './AppStyles';
//Section
import Section from './Section';
//Counter
import Counter from './Counter'; // "это не относиться к заданию"
//Friend list
import FriendList from './FriendList';
import FriendJSON from './FriendList/friends.json';
//SocialProfile
import SocialProfile from './SocialProfile';
import SocialProfileJSON from './SocialProfile/user.json';
//Statistics
import Statistics from './Statistics';
import StatisticsJSON from './Statistics/statistical-data.json';
//TransactionHistory
import TransactionHistory from './TransactionHistory';
import TransactionHistoryJSON from './TransactionHistory/transactions.json';

import SingUpForm from './SingUpForm';

function App() {
	return (
		<>
			<Router>
				<AS.Nav>
					<AS.Ul>
						<AS.Li>
							<Link to="/Goit-React" className="link_item">
								Home
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/counter" className="link_item">
								Counter
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/friend" className="link_item">
								Friend List
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/stats" className="link_item">
								Statistics
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/users" className="link_item">
								Social Profile
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/items" className="link_item">
								Transaction History
							</Link>
						</AS.Li>
						<AS.Li>
							<Link to="/Goit-React/singupform" className="link_item">
								Sing up Form
							</Link>
						</AS.Li>
					</AS.Ul>
				</AS.Nav>

				<Route path="/Goit-React/counter">
					<Counter />
				</Route>
				<Route path="/Goit-React/friend">
					<Section title="Task-1">
						<FriendList friends={FriendJSON} />
					</Section>
				</Route>
				<Route path="/Goit-React/users">
					<Section title="Task-2">
						<SocialProfile user={SocialProfileJSON} />
					</Section>
				</Route>
				<Route path="/Goit-React/stats">
					<Section title="Task-3">
						<Statistics stats={StatisticsJSON} />
					</Section>
				</Route>
				<Route path="/Goit-React/items">
					<Section title="Task-4">
						<TransactionHistory items={TransactionHistoryJSON} />
					</Section>
				</Route>
				<Route path="/Goit-React/singupform">
					<Section title="Task-5">
						<SingUpForm />
					</Section>
				</Route>
			</Router>
			
		</>
	);
}

export default App;
