import styled from 'styled-components';

export default {
  Div: styled.div`
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0 auto;
    margin-top: 10%;
    width: 90%;
    height: auto;
    list-style: none;
    border-radius: 4px;
    background-color: rgb(255, 255, 255);
  `,
  Button: styled.button`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100px;
    height: 50px;
    &:hover {
      background-color: rgb(155, 255, 155);
    }
  `,
  Span: styled.span`
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;

    min-width: 140px;
    height: 50px;
    font-size: 1.5em;
    line-height: 3;
    &:hover {
      background-color: rgb(155, 255, 155);
    }
  `,
};
