import React, { Component } from 'react';

class SignUpForm extends Component {
	state = {
		login: ''
	};

	handleChange = (e) => {
		console.dir(e.target);
		this.setState({ login: e.target.value });
	};
	handleSubmit = (evt) => {
		evt.preventDefault();
		console.log(`Signed up as: ${this.state.login}`);

		// Проп который передается форме для вызова при сабмите
		// this.props.onSubmit(this.state.login);
	};

	render() {
		const { login } = this.state;

		return (
			<form onSubmit={this.handleSubmit}>
				<label>
					Name
					<input type="text" placeholder="Enter login" value={login} onChange={this.handleChange} />
				</label>
				<label>
					Email
					<input type="email" placeholder="Enter login" value={login} onChange={this.handleChange} />
				</label>
				<label>
					Password
					<input type="password" placeholder="Enter login" value={login} onChange={this.handleChange} />
				</label>
				<button type="submit">Sign up as {login}</button>
			</form>
		);
	}
}
export default SignUpForm;
