import React, { Component } from 'react';

import counterStyles from './counterStyles';
const { Div, Button, Span } = counterStyles;
//https://ru.reactjs.org/docs/handling-events.html
//https://ru.reactjs.org/docs/events.html#clipboard-events
class Counter extends Component {
	//Для описания типов пропов используем это
	static propTypes = {};
	//для записи  значений по умолчанию используем это
	static defaultProps = {
		step: 5,
		initialValue: 3
	};
	//старый метод и ES6
	// constructor() {
	// 	// Обезательный функция что бы можно обращаться к this родительскому эелементу Component
	// 	super();
	// }

	state = {
		value: this.props.initialValue,
		st: this.props.step,
		newNum: 10,
		message: new Date().toLocaleTimeString(),
		clock: new Date().toLocaleTimeString(),
		newClock: ''
	};
	// handleIncrement = () => this.setState({ value: (this.state.value + this.state.st) });
	// handleDecrement = () => this.setState({ value: this.state.value - this.state.st });
	handleIncrement = () =>
		this.setState({
			value: this.state.value * this.state.st,
			newNum: this.state.newNum + this.state.st
		});
	handleDecrement = () =>
		this.setState({
			value: this.state.value / this.state.st,
			newNum: this.state.newNum - this.state.st
		});
	//Clock
	upDateTime = () => this.setState({ message: new Date().toLocaleTimeString() });

	upClock() {
		setInterval(() => {
			this.setState({ clock: new Date().toLocaleTimeString() });
		}, 1000);
		return this.state.clock;
	}
	///
	newClock(times) {
		setInterval(() => {
			this.setState({
				newClock: this.downdataClokc(Date.now(), times)
			});
		}, 1000);
		return this.state.newClock;
	}
	pad(val) {
		return String(val).padStart(2, '0');
	}
	downdataClokc(time, hoursCounter = 0) {
		this.daysDown = this.pad(Math.floor(time / (1000 * 60 * 60 * 24)));
		this.hoursDown = this.pad(Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60) + hoursCounter));
		this.minsDown = this.pad(Math.floor((time % (1000 * 60 * 60)) / (1000 * 60)));
		this.secsDown = this.pad(Math.floor((time % (1000 * 60)) / 1000));
		return `${this.hoursDown}:${this.minsDown}:${this.secsDown}`;
	}

	// обезвтельныи методы  который возвращает JSX разметку
	render() {
		return (
			<Div>
				<Button type="button" onClick={this.handleIncrement}>
					Increment by
				</Button>
				<Span>
					{this.state.value}--{this.state.newNum}
				</Span>
				<Button type="button" onClick={this.handleDecrement}>
					Decrement by
				</Button>
				<Div>
					<Button type="button" onClick={this.upDateTime}>
						ClockUp
					</Button>
					<Span>{this.state.message}</Span>
					<Span>{this.upClock()}</Span>
					<Span>{this.newClock(5)}</Span>
				</Div>
			</Div>
		);
	}
}

export default Counter;
